.footer {
  background-color: var(--primary-color);
  color: var(--white-color);
  padding-top: 3rem;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.footer h3, .footer h4 {
  color: var(--white-color);
  margin-bottom: 1.5rem;
}

.footer-info p {
  margin-bottom: 1rem;
}

.footer-contact p {
  margin-bottom: 0.5rem;
}

.footer-bottom {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 1rem 0;
  margin-top: 3rem;
  text-align: center;
}

@media (max-width: 768px) {
  .footer {
    padding-top: 2rem;
  }
  
  .footer-container {
    grid-template-columns: 1fr;
    text-align: center;
  }
}
